package com.hepsiburada.lib.feature.dynamicview

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.runtime.Composable
import androidx.compose.runtime.CompositionLocalProvider
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import com.hepsiburada.lib.feature.dynamicview.sampledata.getColumnRootJson
import com.seiko.imageloader.ImageLoader
import com.seiko.imageloader.ImageLoaderConfigBuilder
import com.seiko.imageloader.LocalImageLoader
import com.seiko.imageloader.cache.memory.maxSizePercent
import com.seiko.imageloader.component.setupDefaultComponents
import com.seiko.imageloader.util.DebugLogger
import com.seiko.imageloader.util.LogPriority

@Composable
fun DynamicComponentWeb(
    json: String? = null
) {

    CompositionLocalProvider(
        LocalImageLoader provides generateImageLoader()
    ) {
        DynamicComponent(
            modifier = Modifier
                .fillMaxSize()
                .background(Color.White),
            json = json
        )
    }
}

private fun generateImageLoader(): ImageLoader {
    return ImageLoader {
        commonConfig()
        components {
            setupDefaultComponents(imageScope)
        }
        interceptor {
            memoryCacheConfig {
                // Set the max size to 25% of the app's available memory.
                maxSizePercent(0.25)
            }
            diskCacheConfig {
//                directory(getCacheDir().resolve("image_cache").toOkioPath())
                maxSizeBytes(512L * 1024 * 1024) // 512MB
            }
        }
    }
}

private fun ImageLoaderConfigBuilder.commonConfig() {
    logger = DebugLogger(LogPriority.VERBOSE)
}

fun getSampleJson() = getColumnRootJson(
    verticalLazyLoadEnabled = false,
    horizontalLazyLoadEnabled = false
)