package com.hepsiburada.lib.feature.dynamicview.extension

import androidx.compose.ui.Alignment
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.text.style.TextDecoration
import com.hepsiburada.lib.feature.dynamicview.model.UIAlignment
import com.hepsiburada.lib.feature.dynamicview.model.UIContentScale
import com.hepsiburada.lib.feature.dynamicview.model.UIFontWeight
import com.hepsiburada.lib.feature.dynamicview.model.UIHorizontalAlignment
import com.hepsiburada.lib.feature.dynamicview.model.UITextAlign
import com.hepsiburada.lib.feature.dynamicview.model.UITextDecoration
import com.hepsiburada.lib.feature.dynamicview.model.UIVerticalAlignment


internal fun UIAlignment?.getUIAlignment(): Alignment =
    when (this) {
        UIAlignment.TopLeft -> Alignment.TopStart
        UIAlignment.TopCenter -> Alignment.TopCenter
        UIAlignment.TopRight -> Alignment.TopEnd
        UIAlignment.CenterLeft -> Alignment.CenterStart
        UIAlignment.Center -> Alignment.Center
        UIAlignment.CenterRight -> Alignment.CenterEnd
        UIAlignment.BottomLeft -> Alignment.BottomStart
        UIAlignment.BottomCenter -> Alignment.BottomCenter
        UIAlignment.BottomRight -> Alignment.BottomEnd
        else -> Alignment.TopStart
    }

internal fun UIVerticalAlignment?.getUIVerticalAlignment(): Alignment.Vertical =
    when (this) {
        UIVerticalAlignment.Top -> Alignment.Top
        UIVerticalAlignment.Bottom -> Alignment.Bottom
        UIVerticalAlignment.Center -> Alignment.CenterVertically
        else -> Alignment.Top
    }

internal fun UIHorizontalAlignment?.getUIHorizontalAlignment(): Alignment.Horizontal =
    when (this) {
        UIHorizontalAlignment.Left -> Alignment.Start
        UIHorizontalAlignment.Right -> Alignment.End
        UIHorizontalAlignment.Center -> Alignment.CenterHorizontally
        else -> Alignment.Start
    }

internal fun UIContentScale?.getUIContentScale(): ContentScale =
    when (this) {
        UIContentScale.Crop -> ContentScale.Crop
        UIContentScale.Fit -> ContentScale.Fit
        else -> ContentScale.Fit
    }

internal fun UITextAlign.getUITextAlign(): TextAlign =
    when (this) {
        UITextAlign.Left -> TextAlign.Start
        UITextAlign.Right -> TextAlign.End
        UITextAlign.Center -> TextAlign.Center
    }

internal fun UIFontWeight.getUIFontWeight(): FontWeight =
    when (this) {
        UIFontWeight.Light -> FontWeight.Light
        UIFontWeight.Regular -> FontWeight.Normal
        UIFontWeight.Medium -> FontWeight.Medium
        UIFontWeight.Bold -> FontWeight.Bold
    }

internal fun UITextDecoration.getUITextDecoration(): TextDecoration =
    when (this) {
        UITextDecoration.None -> TextDecoration.None
        UITextDecoration.LineThrough -> TextDecoration.LineThrough
        UITextDecoration.Underline -> TextDecoration.Underline
    }