package com.hepsiburada.lib.feature.dynamicview.component

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.runtime.Composable
import androidx.compose.runtime.Stable
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.hepsiburada.lib.feature.dynamicview.DynamicUI
import com.hepsiburada.lib.feature.dynamicview.extension.addCommonUIModifiers
import com.hepsiburada.lib.feature.dynamicview.extension.getUIHorizontalAlignment
import com.hepsiburada.lib.feature.dynamicview.extension.orZero
import com.hepsiburada.lib.feature.dynamicview.model.AnalyticsObject
import com.hepsiburada.lib.feature.dynamicview.model.ColumnUIModel
import com.hepsiburada.lib.feature.dynamicview.model.UIModel

@Stable
@Composable
internal fun ColumnUI(
    scope: Any? = null,
    uiModel: UIModel,
    click: (String) -> Unit,
    analyticsEvent: (EventType, List<AnalyticsObject>) -> Unit,
) {
    val uiData = uiModel.current.component as? ColumnUIModel ?: return

    val event = rememberAnalyticsEvent(uiData, analyticsEvent)

    val modifiers = remember {
        mutableStateOf(Modifier.addCommonUIModifiers(
            uiModel = uiModel,
            scope = scope,
            click = {
                event.value.sendClickEvent()
                click.invoke(it)
            }
        ))
    }

    DynamicColumn(
        isLazy = uiData.isLazy,
        isScrollable = uiData.isScrollable == true,
        items = uiData.components,
        horizontalAlignment = uiData.horizontalAlignment.getUIHorizontalAlignment(),
        verticalArrangement = Arrangement.spacedBy(
            uiData.spacing.orZero.dp,
            Alignment.CenterVertically
        ),
        modifier = Modifier
            .then(modifiers.value),
    ) {
        DynamicUI(
            uiModel = UIModel(it, uiData),
            scope = this,
            click = click,
            analyticsEvent = analyticsEvent,
        )
    }

}