package com.hepsiburada.lib.feature.dynamicview.model

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable(with = ComponentSerializer::class)
data class ComponentUIModel(
    @SerialName("type")
    val type: UIType? = null,

    @SerialName("data")
    val component: Any? = null,
)

@Serializable
open class CommonUIModel(
    @SerialName("common")
    var commonProperties: CommonPropertiesModel? = null,
)

@Serializable
data class CommonPropertiesModel(
    @SerialName("compKey")
    var compKey: String? = null,

    @SerialName("width")
    var width: SizeInfoModel? = null,

    @SerialName("height")
    var height: SizeInfoModel? = null,

    @SerialName("ratio")
    var ratio: Float? = null,

    @SerialName("weight")
    var weight: Float? = null,

    @SerialName("margin")
    var margin: UIMarginModel? = null,

    @SerialName("padding")
    var padding: UIMarginModel? = null,

    @SerialName("cornerRadius")
    var cornerRadius: UICornerRadiusModel? = null,

    @SerialName("backgroundColor")
    var backgroundColor: String? = null,

    @SerialName("border")
    var border: BorderModel? = null,

    @SerialName("shadow")
    var shadow: UIShadowModel? = null,

    @SerialName("deeplink")
    var deeplink: String? = null,

    @SerialName("analytic")
    var analytic: AnalyticsModel? = null,

    @SerialName("mask")
    var mask: UIMask? = null,
)

@Serializable
data class BoxUIModel(
    @SerialName("alignment")
    val alignment: UIAlignment? = null,

    @SerialName("items")
    val components: List<ComponentUIModel>? = null,
) : CommonUIModel()

@Serializable
data class ColumnUIModel(
    @SerialName("spacing")
    val spacing: Float? = null,

    @SerialName("horizontalAlignment")
    val horizontalAlignment: UIHorizontalAlignment? = null,

    @SerialName("isScrollable")
    val isScrollable: Boolean? = null,

    @SerialName("items")
    val components: List<ComponentUIModel>? = null,
) : CommonUIModel() {


    //    @Transient
    var isLazy: Boolean = false
}

@Serializable
data class RowUIModel(
    @SerialName("spacing")
    val spacing: Float? = null,

    @SerialName("verticalAlignment")
    val verticalAlignment: UIVerticalAlignment? = null,

    @SerialName("isScrollable")
    val isScrollable: Boolean? = null,

    @SerialName("items")
    val components: List<ComponentUIModel>? = null,
) : CommonUIModel() {

    //    @Transient
    var isLazy: Boolean = false
}

@Serializable
data class ImageUIModel(
    @SerialName("contentScale")
    val contentScale: UIContentScale? = null,

    @SerialName("url")
    val url: String? = null,
) : CommonUIModel()

@Serializable
data class TextUIModel(
    @SerialName("text")
    val text: String? = null,

    @SerialName("color")
    val color: String? = null,

    @SerialName("alignment")
    val alignment: UITextAlign? = null,

    @SerialName("fontSize")
    val fontSize: Float? = null,

    @SerialName("fontWeight")
    val fontWeight: UIFontWeight? = null,

    @SerialName("textDecoration")
    val textDecoration: UITextDecoration? = null,

    @SerialName("maxLines")
    val maxLines: Int? = null, // Float?
) : CommonUIModel()

@Serializable
data class SizeInfoModel(
    @SerialName("min")
    val min: Float? = null,
    @SerialName("max")
    val max: Float? = null,
    @SerialName("value")
    val value: Float? = null,
    @SerialName("fill")
    val fill: Boolean? = null,
)

@Serializable
data class BorderModel(
    @SerialName("width")
    val width: Float? = null,
    @SerialName("color")
    val color: String? = null,
)

@Serializable
data class AnalyticsModel(
    @SerialName("view")
    val view: List<AnalyticsObject>? = null,

    @SerialName("click")
    val click: List<AnalyticsObject>? = null,
)

@Serializable
data class AnalyticsObject(
    @SerialName("eventName")
    val eventName: String? = null,

    @SerialName("properties")
    val properties: String? = null,
)

@Serializable
data class UIShadowModel(
    @SerialName("color")
    val color: String? = null,

    @SerialName("radius")
    val radius: Float? = null,

    @SerialName("opacity")
    val opacity: Float? = null,

    @SerialName("offsetX")
    val offsetX: Float? = null,

    @SerialName("offsetY")
    val offsetY: Float? = null,
)

@Serializable
data class UIMarginModel(
    @SerialName("top")
    val top: Float? = null,

    @SerialName("bottom")
    val bottom: Float? = null,

    @SerialName("left")
    val left: Float? = null,

    @SerialName("right")
    val right: Float? = null,
)


@Serializable
data class UICornerRadiusModel(
    @SerialName("topRight")
    val topRight: Float? = null,

    @SerialName("topLeft")
    val topLeft: Float? = null,

    @SerialName("bottomRight")
    val bottomRight: Float? = null,

    @SerialName("bottomLeft")
    val bottomLeft: Float? = null,
)

@Serializable
enum class UIType {
    @SerialName("ZBox")
    Box,

    @SerialName("VerticalList")
    Column,

    @SerialName("HorizontalList")
    Row,

    @SerialName("Image")
    Image,

    @SerialName("Text")
    Text,
}

@Serializable
enum class UITextDecoration {
    @SerialName("none")
    None,

    @SerialName("lineThrough")
    LineThrough,

    @SerialName("underline")
    Underline,
}

@Serializable
enum class UIFontWeight {
    @SerialName("light")
    Light,

    @SerialName("regular")
    Regular,

    @SerialName("medium")
    Medium,

    @SerialName("bold")
    Bold,
}

@Serializable
enum class UIAlignment {
    @SerialName("topLeft")
    TopLeft,

    @SerialName("topCenter")
    TopCenter,

    @SerialName("topRight")
    TopRight,

    @SerialName("centerLeft")
    CenterLeft,

    @SerialName("center")
    Center,

    @SerialName("centerRight")
    CenterRight,

    @SerialName("bottomLeft")
    BottomLeft,

    @SerialName("bottomCenter")
    BottomCenter,

    @SerialName("bottomRight")
    BottomRight,
}

@Serializable
enum class UIHorizontalAlignment {
    @SerialName("left")
    Left,

    @SerialName("right")
    Right,

    @SerialName("center")
    Center,
}

@Serializable
enum class UIVerticalAlignment {
    @SerialName("top")
    Top,

    @SerialName("bottom")
    Bottom,

    @SerialName("center")
    Center,
}

@Serializable
enum class UITextAlign {
    @SerialName("left")
    Left,

    @SerialName("right")
    Right,

    @SerialName("center")
    Center,
}

@Serializable
enum class UIContentScale {
    @SerialName("fill")
    Crop,

    @SerialName("fit")
    Fit,
}

@Serializable
enum class UIMask {
    @SerialName("none")
    None,

    @SerialName("rectangle")
    Rectangle,

    @SerialName("circle")
    Circle,
}