package com.hepsiburada.lib.feature.dynamicview.component

import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.MutableState
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import com.hepsiburada.lib.feature.dynamicview.model.AnalyticsObject
import com.hepsiburada.lib.feature.dynamicview.model.CommonUIModel

@Composable
internal fun rememberAnalyticsEvent(
    commonUIModel: CommonUIModel,
    analyticsEvent: (EventType, List<AnalyticsObject>) -> Unit
): MutableState<AnalyticsEvent> {

    val event = remember { mutableStateOf(AnalyticsEvent(commonUIModel, analyticsEvent)) }

    commonUIModel.commonProperties?.analytic?.view?.let {
        LaunchedEffect(Unit) {
            analyticsEvent.invoke(EventType.VIEW, it)
        }
    }

    return event
}

internal class AnalyticsEvent(
    private val commonUIModel: CommonUIModel,
    private val analyticsEvent: (EventType, List<AnalyticsObject>) -> Unit
) {
    fun sendClickEvent() {
        commonUIModel.commonProperties?.analytic?.click?.let {
            analyticsEvent.invoke(EventType.CLICK, it)
        }
    }
}

internal enum class EventType {
    VIEW, CLICK
}