package com.hepsiburada.lib.feature.dynamicview.util

import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.resources.resource


@OptIn(ExperimentalResourceApi::class)
internal suspend fun fileResource(res: String): String {
    return resource(res).readBytes().decodeToString()
}

internal fun getRandomString(length: Int = 10): String {
    val allowedChars = ('A'..'Z') + ('a'..'z') + ('0'..'9')
    return (1..length)
        .map { allowedChars.random() }
        .joinToString("")
}