package com.hepsiburada.lib.feature.dynamicview

import androidx.compose.runtime.Composable
import androidx.compose.ui.graphics.painter.Painter


object DeviceUtils {
    val screenSizeDp by lazy {
        getScreenSizeDp()
    }
    val screenSizePx by lazy {
        getScreenSizePx()
    }
}

data class ScreenSize(var width: Int, var height: Int)

expect fun getScreenSizeDp(): ScreenSize

expect fun getScreenSizePx(): ScreenSize

@Composable
internal expect fun rememberImagePainter(url: String): Painter