import kotlinx.coroutines.launch
import kotlinx.serialization.decodeFromString

@Composable
internal fun DynamicComponent(
    modifier: Modifier = Modifier,
    json: String? = null,
    toastt: ((String) -> Unit) = {}
) {
    if (json == null) return

    val componentState = remember { mutableStateOf<ComponentUIModel?>(null) }

    val jsonState = remember { mutableStateOf(json) }.apply { value = json }

    val scope = rememberCoroutineScope()

    LaunchedEffect(Unit) {
        scope.launch {

//            if (jsonState.value.length < 5) {
                // Sample Data Source: Object
//            val json = getColumnRootJson(
//                verticalLazyLoadEnabled = false,
//                horizontalLazyLoadEnabled = false
//            )

                // Sample Data Source: Json File
                jsonState.value = fileResource(Res.raw.dynamicview)
//            }

//            toastt.invoke(jsonState.value.length.toString())

            componentState.value = try {
                jsonKt.decodeFromString<ComponentUIModel>(jsonState.value)
            } catch (t: Throwable) {
                null
            }
        }
    }

    componentState.value?.let {
        Surface(
            modifier = modifier
        ) {
            DynamicUI(
                uiModel = UIModel(it, null),
                scope = null,
                click = {
                    println("dynamicview -> click: $it")
                },
                analyticsEvent = { type, eventList ->
                    println("dynamicview -> event: $type / $eventList")
                },
            )
        }
    }


}

@Composable
internal fun DynamicUI(
    uiModel: UIModel,
    scope: Any? = null,
    click: (String) -> Unit,
    analyticsEvent: (EventType, List<AnalyticsObject>) -> Unit,
) {
    if (uiModel.current.component == null) return

    when (uiModel.current.type) {
        UIType.Box -> BoxUI(
            scope = scope,
            uiModel = uiModel,
            click = click,
            analyticsEvent = analyticsEvent
        )

        UIType.Column -> ColumnUI(
            scope = scope,
            uiModel = uiModel,
            click = click,
            analyticsEvent = analyticsEvent
        )

        UIType.Row -> RowUI(
            scope = scope,
            uiModel = uiModel,
            click = click,
            analyticsEvent = analyticsEvent
        )

        UIType.Image -> ImageUI(
            scope = scope,
            uiModel = uiModel,
            click = click,
            analyticsEvent = analyticsEvent
        )

        UIType.Text -> TextUI(
            scope = scope,
            uiModel = uiModel,
            click = click,
            analyticsEvent = analyticsEvent
        )

        else -> Unit
    }
}