package com.hepsiburada.lib.feature.dynamicview.model

import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.KSerializer
import kotlinx.serialization.descriptors.SerialDescriptor
import kotlinx.serialization.descriptors.buildClassSerialDescriptor
import kotlinx.serialization.descriptors.nullable
import kotlinx.serialization.encoding.Decoder
import kotlinx.serialization.encoding.Encoder
import kotlinx.serialization.encoding.decodeStructure
import kotlinx.serialization.encoding.encodeStructure

object ComponentSerializer : KSerializer<ComponentUIModel> {

    override val descriptor: SerialDescriptor = buildClassSerialDescriptor("ComponentUIModel") {
        element("type", UIType.serializer().descriptor.nullable)
        element("data", buildClassSerialDescriptor("Any").nullable)
    }

    private val serializers = mapOf(
        UIType.Box to BoxUIModel.serializer(),
        UIType.Column to ColumnUIModel.serializer(),
        UIType.Row to RowUIModel.serializer(),
        UIType.Image to ImageUIModel.serializer(),
        UIType.Text to TextUIModel.serializer(),
    )

    @Suppress("UNCHECKED_CAST")
    private fun getSerializer(
        uiType: UIType?
    ) = if (serializers.containsKey(uiType)) serializers[uiType] as? KSerializer<Any> else null

    @OptIn(ExperimentalSerializationApi::class)
    override fun serialize(
        encoder: Encoder,
        value: ComponentUIModel
    ) = encoder.encodeStructure(descriptor) {
        encodeNullableSerializableElement(descriptor, 0, UIType.serializer(), value.type)
        val serializer = getSerializer(value.type) ?: return@encodeStructure
        encodeNullableSerializableElement(descriptor, 1, serializer, value.component)
    }

    override fun deserialize(
        decoder: Decoder
    ) = decoder.decodeStructure(descriptor) {
        val uiType = if (decodeElementIndex(descriptor) == 0) {
            decodeSerializableElement(descriptor, 0, UIType.serializer())
        } else null

        val serializer = getSerializer(uiType)

        val component =
            if (decodeElementIndex(descriptor) == 1 && uiType != null && serializer != null) {
                decodeSerializableElement(descriptor, 1, serializer)
            } else null

        ComponentUIModel(uiType, component)
    }

}