package com.hepsiburada.lib.feature.dynamicview.sampledata

import com.hepsiburada.lib.feature.dynamicview.model.AnalyticsModel
import com.hepsiburada.lib.feature.dynamicview.model.AnalyticsObject
import com.hepsiburada.lib.feature.dynamicview.model.CommonPropertiesModel
import com.hepsiburada.lib.feature.dynamicview.model.ComponentUIModel
import com.hepsiburada.lib.feature.dynamicview.model.ImageUIModel
import com.hepsiburada.lib.feature.dynamicview.model.RowUIModel
import com.hepsiburada.lib.feature.dynamicview.model.SizeInfoModel
import com.hepsiburada.lib.feature.dynamicview.model.UIContentScale
import com.hepsiburada.lib.feature.dynamicview.model.UICornerRadiusModel
import com.hepsiburada.lib.feature.dynamicview.model.UIMarginModel
import com.hepsiburada.lib.feature.dynamicview.model.UIType
import com.hepsiburada.lib.feature.dynamicview.util.getRandomString

private fun compImage1(url: String) = ImageUIModel(
    contentScale = UIContentScale.Fit,
    url = url
).also {
    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.analytic = AnalyticsModel(
            view = arrayListOf(
                AnalyticsObject("view_EventName_1", "view_EventProperties_1"),
                AnalyticsObject("view_EventName_2", "view_EventProperties_2")
            ),
            click = arrayListOf(
                AnalyticsObject("click_EventName_1", "click_EventProperties_1"),
                AnalyticsObject("click_EventName_2", "click_EventProperties_2")
            ),
        )
        it.cornerRadius = UICornerRadiusModel(0f, 0f, 0f, 0f)
        it.ratio = null
        it.padding = UIMarginModel(0f, 0f, 0f, 0f)
        it.weight = 1f
        it.backgroundColor = null
        it.height = SizeInfoModel(fill = true)
        it.margin = UIMarginModel(4f, 4f, 4f, 4f)
        it.width = null
        it.shadow = null
        it.deeplink = url
    }
}

private val row1 = RowUIModel(
    isScrollable = false,
    components = arrayListOf(
        "https://images.hepsiburada.net/banners/s/0/1440-450/cok_satan_2132911229798461172.png/format:webp",
        "https://images.hepsiburada.net/banners/s/0/1440-450/yeni132911230414648928.png/format:webp",
        "https://images.hepsiburada.net/banners/s/0/1440-450/tumupng132911230822838867.png/format:webp"

    ).map {
        ComponentUIModel(
            UIType.Image,
            compImage1(it)
        )
    },
).also {
    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.width = SizeInfoModel(fill = true)
        it.height = SizeInfoModel(value = 56f)
        it.ratio = null
        it.weight = null
        it.margin = UIMarginModel(0f, 0f, 0f, 0f)
        it.padding = UIMarginModel(4f, 4f, 4f, 4f)
        it.cornerRadius = UICornerRadiusModel(0f, 0f, 0f, 0f)
        it.backgroundColor = null
        it.shadow = null
        it.deeplink = null
        it.analytic = null
    }
}

internal fun getRow4() = ComponentUIModel(UIType.Row, row1)