package com.hepsiburada.lib.feature.dynamicview.component

import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.Stable
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.TextUnit
import androidx.compose.ui.unit.sp
import com.hepsiburada.lib.feature.dynamicview.extension.addCommonUIModifiers
import com.hepsiburada.lib.feature.dynamicview.extension.getUIFontWeight
import com.hepsiburada.lib.feature.dynamicview.extension.getUITextAlign
import com.hepsiburada.lib.feature.dynamicview.extension.getUITextDecoration
import com.hepsiburada.lib.feature.dynamicview.extension.toColor
import com.hepsiburada.lib.feature.dynamicview.model.AnalyticsObject
import com.hepsiburada.lib.feature.dynamicview.model.TextUIModel
import com.hepsiburada.lib.feature.dynamicview.model.UIModel

@Stable
@Composable
internal fun TextUI(
    scope: Any? = null,
    uiModel: UIModel,
    click: (String) -> Unit,
    analyticsEvent: (EventType, List<AnalyticsObject>) -> Unit,
) {
    val uiData = uiModel.current.component as? TextUIModel ?: return

    val event = rememberAnalyticsEvent(uiData, analyticsEvent)

    val modifiers = remember {
        mutableStateOf(Modifier.addCommonUIModifiers(
            uiModel = uiModel,
            scope = scope,
            click = {
                event.value.sendClickEvent()
                click.invoke(it)
            }
        ))
    }

    Text(
        text = uiData.text.orEmpty(),
        color = uiData.color.toColor() ?: Color.Unspecified,
        fontSize = uiData.fontSize?.sp ?: TextUnit.Unspecified,
        textAlign = uiData.alignment?.getUITextAlign(),
        fontWeight = uiData.fontWeight?.getUIFontWeight(),
        textDecoration = uiData.textDecoration?.getUITextDecoration(),
        maxLines = uiData.maxLines.let { if (it != null && it > 0) it else Int.MAX_VALUE },
        modifier = Modifier
            .then(modifiers.value),
    )
}