//        getColumn4(),
//        getRow2(),
//        getColumn1(),
//        getColumn1(),
//        getColumn1(),
    )
).also {
    it.isLazy = SampleDataConfig.verticalLazyLoadEnabled

    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.width = SizeInfoModel(fill = true)
        it.height = null
        it.ratio = null
        it.weight = null
        it.margin = UIMarginModel(0f, 0f, 0f, 0f)
        it.padding = null
        it.cornerRadius = UICornerRadiusModel(0f, 0f, 0f, 0f)
        it.backgroundColor = "#ffffff"
        it.shadow = null
        it.deeplink = null
        it.analytic = null
    }
}

private fun getColumnRoot() = ComponentUIModel(UIType.Column, columnRoot)

internal fun getColumnRootJson(
    verticalLazyLoadEnabled: Boolean,
    horizontalLazyLoadEnabled: Boolean
): String {
    SampleDataConfig.verticalLazyLoadEnabled = verticalLazyLoadEnabled
    SampleDataConfig.horizontalLazyLoadEnabled = horizontalLazyLoadEnabled
    return jsonKt.encodeToString(getColumnRoot())
}

internal object SampleDataConfig {
    var verticalLazyLoadEnabled = false
    var horizontalLazyLoadEnabled = false
}


