val rowData1 = arrayListOf(
    "Çok Satan Kitaplar" to "https://images.hepsiburada.net/banners/s/0/216-120/Cok_Satan_Kitaplar132973649638878367.png/format:webp",
    "Yeni Çıkan Kitaplar" to "https://images.hepsiburada.net/banners/s/0/216-120/Yeni_Cikan_Kitaplar132973650152140252.png/format:webp",
    "Yaz Okuma Listeni Oluştur" to "https://images.hepsiburada.net/banners/s/0/216-120/Plaj,_Bahce_ve_daha_fazlasi_icin_Yaz_Okuma_Listeni_Olustur132973651590056421.png/format:webp",
    "Edebiyat Kitapları" to "https://images.hepsiburada.net/banners/s/0/216-120/Edebiyat_Kitaplari132973651388574471.png/format:webp"
)

val rowData2 = arrayListOf(
    "Çocuk Kitapları" to "https://images.hepsiburada.net/banners/s/0/216-120/Cocuk_Kitaplari132973651824538466.png/format:webp",
    "Eğitim Kitapları" to "https://images.hepsiburada.net/banners/s/0/216-120/Egitim_Kitaplari132973652019949557.png/format:webp",
    "Sınav Kitapları" to "https://images.hepsiburada.net/banners/s/0/216-120/Sinav_Kitaplari132973653398564819.png/format:webp",
    "Kişisel Gelişim Kitapları" to "https://images.hepsiburada.net/banners/s/0/216-120/Kisisel_Gelisim_Kitaplari132973653930616738.png/format:webp",
)

val rowData3 = arrayListOf(
    "Akademik Kitaplar" to "https://images.hepsiburada.net/banners/s/0/216-120/Akademik_Kitaplar132973654119218093.png/format:webp",
    "Yabancı Dil Kitaplar" to "https://images.hepsiburada.net/banners/s/0/216-120/Yabanci_Dil_Kitaplar132973654324799378.png/format:webp",
    "Araştırma & Tarih Kitapları" to "https://images.hepsiburada.net/banners/s/0/216-120/Arastirma_&_Tarih_Kitaplari132973654878013759.png/format:webp",
    "Çizgi Roman ve Mizah Kitapları" to "https://images.hepsiburada.net/banners/s/0/216-120/Cizgi_Roman_ve_Mizah_Kitaplari132973655154218671.png/format:webp",
)

internal fun getColumn3() = ComponentUIModel(UIType.Column, compColumn1())

private fun compColumn1() = ColumnUIModel(
    horizontalAlignment = UIHorizontalAlignment.Left,
    components = arrayListOf(
        rowData1,
        rowData2,
        rowData3,
    ).map {
        ComponentUIModel(
            UIType.Row,
            compRow1(it)
        )
    },
).also {
    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.width = null
        it.height = null
        it.ratio = null
        it.weight = null
        it.margin = UIMarginModel(0f, 0f, 0f, 0f)
        it.padding = UIMarginModel(4f, 4f, 4f, 4f)
        it.cornerRadius = UICornerRadiusModel(0f, 0f, 0f, 0f)
        it.backgroundColor = null
        it.shadow = null
        it.deeplink = null
        it.analytic = null
    }
}

private fun compRow1(items: List<Pair<String, String>>) = RowUIModel(
    isScrollable = false,
    components = items.map {
        ComponentUIModel(
            UIType.Column,
            compColumn2(it)
        )
    },
).also {
    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.width = null
        it.height = null
        it.ratio = null
        it.weight = null
        it.margin = UIMarginModel(0f, 0f, 0f, 0f)
        it.padding = UIMarginModel(0f, 0f, 0f, 0f)
        it.cornerRadius = UICornerRadiusModel(0f, 0f, 0f, 0f)
        it.backgroundColor = null
        it.shadow = null
        it.deeplink = null
        it.analytic = null
    }
}

private fun compColumn2(item: Pair<String, String>) = ColumnUIModel(
    horizontalAlignment = UIHorizontalAlignment.Left,
    components = arrayListOf(
        ComponentUIModel(
            UIType.Image,
            compImage1(item.second)
        ),
        ComponentUIModel(
            UIType.Text,
            compText1(item.first)
        )
    ),
).also {
    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.width = null
        it.height = null
        it.ratio = 1f
        it.weight = 1f
        it.margin = UIMarginModel(4f, 4f, 4f, 4f)
        it.padding = UIMarginModel(0f, 0f, 0f, 0f)
        it.cornerRadius = UICornerRadiusModel(8f, 8f, 8f, 8f)
        it.backgroundColor = "#ffffff"
        it.shadow = UIShadowModel("#000000", 2f, 0.6f)
        it.deeplink = null
        it.analytic = null
        it.border = null
    }
}

private fun compImage1(url: String) = ImageUIModel(
    contentScale = UIContentScale.Fit,
    url = url
).also {
    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.analytic = null
        it.ratio = null
        it.weight = null
        it.cornerRadius = UICornerRadiusModel(8f, 8f, 8f, 8f)
        it.backgroundColor = null
        it.height = null
        it.margin = UIMarginModel(4f, 4f, 4f, 4f)
        it.padding = UIMarginModel(0f, 0f, 0f, 0f)
        it.width = null
        it.shadow = null
        it.deeplink = null
    }
}

private fun compText1(text: String) = TextUIModel(
    text = text,
    color = "#484848",
    fontSize = 11f,
    alignment = UITextAlign.Center,
    fontWeight = UIFontWeight.Bold,
    textDecoration = null,
    maxLines = 2,
).also {
    it.commonProperties = CommonPropertiesModel().also {
        it.compKey = getRandomString()
        it.analytic = null
        it.ratio = null
        it.weight = null
        it.cornerRadius = UICornerRadiusModel(8f, 8f, 8f, 8f)
        it.height = null
        it.margin = UIMarginModel(4f, 4f, 4f, 4f)
        it.padding = UIMarginModel(0f, 0f, 0f, 0f)
        it.width = SizeInfoModel(fill = true)
        it.shadow = null
        it.deeplink = null
    }
}