package com.hepsiburada.lib.feature.dynamicview.component

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.lazy.rememberLazyListState
import androidx.compose.foundation.rememberScrollState
import androidx.compose.foundation.verticalScroll
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import com.hepsiburada.lib.feature.dynamicview.extension.addModifier
import com.hepsiburada.lib.feature.dynamicview.model.CommonUIModel

@Composable
internal fun <T> DynamicColumn(
    modifier: Modifier = Modifier,
    isLazy: Boolean = false,
    isScrollable: Boolean = false,
    verticalArrangement: Arrangement.Vertical = Arrangement.Top,
    horizontalAlignment: Alignment.Horizontal = Alignment.Start,
    items: List<T>? = null,
    content: @Composable Any.(item: T) -> Unit
) {

    if (isLazy) {

        LazyColumn(
            state = rememberLazyListState(),
            horizontalAlignment = horizontalAlignment,
            verticalArrangement = verticalArrangement,
            modifier = modifier,
        ) {
            items?.forEach {
                item(
                    key = if (it is CommonUIModel) it.commonProperties?.compKey else null,
                    content = {
                        content.invoke(this, it)
                    }
                )
            }
        }

    } else {

        val scrollState = rememberScrollState()

        Column(
            horizontalAlignment = horizontalAlignment,
            verticalArrangement = verticalArrangement,
            modifier = Modifier
                .addModifier(isScrollable) {
                    verticalScroll(scrollState)
                }
                .then(modifier),
        ) {

            items?.forEach {
                content.invoke(this, it)
            }
        }

    }

}